body,
html {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  line-height: 1;
}

.App {
  padding: 0;
  margin: 1% auto;
  box-sizing: border-box;
  font-family: 'Helvetica', 'Heebo', sans-serif;
  font-size: 1rem;
  /* font-weight: 800; */
  scroll-behavior: smooth;
}

.sticky-top {
  position: fixed;
  top: 0;
  width: 100%;
}

.border-box {
  box-sizing: border-box;
}

/* div {
  border: 1px green solid;
} */
.dis-flex {
  display: flex;
}
.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.d-none {
  display: none;
}

#politics,
#world-news,
#cdn-news,
#popular,
#sports,
#real-estate,
#entertainment {
  scroll-margin-top: 4rem;
}

@media only screen and (max-width: 600px) {
  .App {
    width: 100%;
  }

  .sticky-top {
    width: 100%;
  }
  .post-item {
    text-align: left;
  }
  .post-item-minor {
    text-align: left;
  }
}

@media only screen and (min-width: 600px) {
  .App {
    width: 100%;
  }

  .sticky-top {
    width: 100%;
  }
}

@media only screen and (min-width: 768px) {
  .App {
    width: 98%;
  }

  .sticky-top {
    width: 98%;
  }
}

@media only screen and (min-width: 1000px) {
  .App {
    width: 85%;
  }

  .sticky-top {
    width: 85%;
  }
}

@media only screen and (min-width: 1600px) {
  .App {
    width: 70%;
  }

  .sticky-top {
    width: 70%;
  }
}
